<template>
  <div class="auth-wrapper auth-v2">
    <b-row class="auth-inner m-0">

      <!-- Left Text-->
      <b-col
          lg="8"
          class="d-none d-lg-flex align-items-center p-0"
      >
        <div class="h-100 w-100 cover-image" :style="{ backgroundImage: 'url(' + imgUrl + ')' }">

        </div>
      </b-col>
      <!-- /Left Text-->

      <!-- Forgot password-->
      <b-col
          lg="4"
          class="d-flex align-items-center auth-bg px-2 p-lg-5"
      >
        <b-col
            sm="8"
            md="6"
            lg="12"
            class="px-xl-2 mx-auto d-flex align-items-center flex-column"
        >
          <b-link class="logo">
            <b-img
                :src="appLogoImage"
                alt="logo"
            />
          </b-link>

          <div>
            <b-card-title class="mb-1">
              Forgot Password?
            </b-card-title>
            <b-card-text class="mb-2">
              Enter your email and we'll send you instructions to reset your password
            </b-card-text>

            <!-- form -->
            <validation-observer ref="simpleRules">
              <b-form
                  class="auth-forgot-password-form mt-2"
                  @submit.prevent="validationForm"
              >
                <b-form-group
                    label="Email"
                    label-for="forgot-password-email"
                >
                  <validation-provider
                      #default="{ errors }"
                      name="Email"
                      rules="required|email"
                  >
                    <b-form-input
                        id="forgot-password-email"
                        v-model="userEmail"
                        :state="errors.length > 0 ? false:null"
                        name="forgot-password-email"
                        placeholder="john@example.com"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>

                <b-button
                    type="submit"
                    variant="primary"
                    block
                >
                  Send reset link
                </b-button>
              </b-form>
            </validation-observer>

            <p class="text-center mt-2">
              <b-link :to="{name:'auth-login'}">
                <feather-icon icon="ChevronLeftIcon" /> Back to login
              </b-link>
            </p>
          </div>
        </b-col>
      </b-col>
      <!-- /Forgot password-->
    </b-row>
  </div>
</template>

<script>
/* eslint-disable global-require */
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BRow, BCol, BLink, BCardTitle, BCardText, BImg, BForm, BFormGroup, BFormInput, BButton,
} from 'bootstrap-vue'
import { required, email } from '@validations'
import store from '@/store/index'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import {$themeConfig} from "@themeConfig";

export default {
  components: {
    // VuexyLogo,
    BRow,
    BCol,
    BLink,
    BImg,
    BForm,
    BButton,
    BFormGroup,
    BFormInput,
    BCardTitle,
    BCardText,
    ValidationProvider,
    ValidationObserver,
  },
  data() {
    return {
      userEmail: '',
      sideImg: require('@/assets/images/pages/forgot-pw-image.jpg'),
      // validation
      required,
      email,
    }
  },
  computed: {
    imgUrl() {
      return this.sideImg
    },
  },
  methods: {
    validationForm() {
      this.$refs.simpleRules.validate().then(success => {
        if (success) {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: 'This is for UI purpose only.',
              icon: 'EditIcon',
              variant: 'success',
            },
          })
        }
      })
    },
  },
  setup() {
    // App Name
    const { appName, appLogoImage } = $themeConfig.app
    return {
      appName,
      appLogoImage,
    }
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/pages/page-auth.scss';
</style>
